.projects-container {
    max-width: 940px;
    margin: 0 32px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 50px;

}

.project-container img {
    width: 450px;
    box-shadow: 0 1px 5px rgb(0 0 0 / 5%), 0 5px 30px rgb(0 0 0 / 20%);
    border-radius: 3px;
}

.project-container div:first-child {
    box-shadow: 0 1px 5px rgb(0 0 0 / 5%), 0 5px 30px rgb(0 0 0 / 20%);
    border-radius: 3px;
    height: 320px;
    width: 430px;
}

.project-container .grease {
    background-image: url('../../assets/grease.png');
    background-position: center 1%;
    background-size: 104%;
}

.project-container .bubba {
    background-image: url('../../assets/bubba.png');
    background-size: 100%;
    background-position: center 1%;
}

.project-container h4{
    font-family: "Rubik";
    font-weight: 400 !important;
    }

.project-container .content {
    text-align: left;
    padding-top: 15px;
}

.project-container .content p {
    color: var(--grey);
}

.project-container .content ul {
    margin-top: 0;
    list-style-type: none;
    padding-left: 0;
    text-transform: uppercase;
    margin-left: 0;
    font-size: 1.2em;
    color: var(--lightgray);
}

.c {
    padding-bottom:  1.5rem;
    padding-left:  1.5rem;
    padding-right:  1.5rem;
}

@media only screen and  (max-width: 769px) and (min-width: 450px){
 
    .project-container {
      max-width: 640px;
      display: grid;
      grid-template-columns: 1fr;
    }

.project-container  div:first-of-type {
  width: 100%;
  height: 0;
  height: 220px;
}

}

@media only screen and (max-width:449px) {
    .project-container div:first-of-type {
        height: 199px;
        width: 100%;
     }
 }
 
 
 