.css
{
  animation-delay: 2s;
}

.html
{
  animation-delay: 4s;
}
.javascript {
    animation-delay: 6s;
}
.adobe {
    animation-delay: 8s;
}
.node {
    animation-delay: 10s;
}

.bootstrap {
    animation-delay: 12s;
}

.jquery {
    animation-delay: 14s;
}

.git {
    animation-delay: 16s;
}

.azure {
    animation-delay: 18s;
}

.vue {
    animation-delay: 20s;
}

.react {
    animation-delay: 22s;
}





@media only screen and (max-width:767px) {
    .about-container {
        max-width: 640px;
        display: grid;
        grid-template-columns: 1fr;
      }
      .skills {
        display: grid;
        gap: 12px;
        grid-template-rows: 1fr;
        justify-items: start;
    }
  }