.App {
  text-align: center;
}

.App.fullHeight {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.main {
  flex: 1;
  margin: auto;
  max-width: 940px;
  padding-bottom: 1.5em;
  padding-left: 1.5em;
  padding-right: 1.5em;
}

@font-face {
  font-family: 'Rubik';
  font-style: normal;
  font-weight: medium;
  src: local('Rubik Medium'), url('assets/Rubik-Medium.ttf') format('truetype');
  }
  

  @font-face {
    font-family: 'Rubik';
  font-style: normal;
  font-weight: 300;
  src: local('Rubik Light'), url('assets/Rubik-Light.ttf') format('truetype');
  }


  @font-face {
    font-family: 'Rubik';
  font-style: normal;
  font-weight: 400;
  src: local('Rubik Regular'), url('assets/Rubik-Regular.ttf') format('truetype');
  }