circle {
	fill: none;
	stroke-width: 3.5;
	animation-name: preloader;
	animation-duration: 3s;
	animation-iteration-count: infinite;
	animation-timing-function: ease-in-out;
	transform-origin: 170px 170px;
	will-change: transform;
}
	circle:nth-of-type(1) {
		stroke-dasharray: 550px;
	}

	circle:nth-of-type(2) {
		stroke-dasharray: 500px;
	}

	circle:nth-of-type(3) {
		stroke-dasharray: 450px;
	}

	circle:nth-of-type(4) {
		stroke-dasharray: 300px;
	}

    .spin-container {
        display: flex;
        align-items: center;
        justify-content: center;
height: 100px;
margin-top: 80px;
    }
    
    .spin-loader {
        max-width: 15rem;
        width: 100%;
        height: auto;
        stroke-linecap: round;
    }

@keyframes preloader {
	50% {
		transform: rotate(360deg);
	}
}


/* NI CARD OVERLAY */
.card-overlay {
    display: inline-block;
    position: relative;
    max-width: 100%;
    vertical-align: middle;
    overflow: hidden;
    -webkit-transform: translateZ(0);
    transform: translateZ(0);
    margin: 0;
  }
  
  .card-overlay h3 {
    text-transform: uppercase;
    line-height: 1.1
  }
  
  .card-overlay p {
    font-size: 18px
  }
  
  .card-overlay>:first-child {
    margin-bottom: 0
  }
  
  .card-overlay-panel>:last-child {
    margin-bottom: 0;
    opacity: 0;
  }
  
  .card-overlay-panel {
    position: absolute;
    top: 55px;
    bottom: 0;
    left: 0;
    right: 0;
    padding: 9px 18px 0 18px;
    height: 100%;
    text-align: left;
    color: #fff;
  }
  
  .card-overlay-hover:not(:hover) .card-overlay-panel {
    opacity: .9
  }
  
  .card-overlay-active>.card-overlay-panel {
    opacity: .9
  }

  .project-container.card-overlay.card-overlay-hover.card:hover .card-overlay-panel>:last-child{
      opacity: 1;
      transition-duration: .8s;
      transition-timing-function: ease-out;
      transition-property: opacity, transform, filter;
  }
  
  .card-overlay-background {
    background: rgba(37, 53, 81, 0.9)
    /* background: var(--teal, 0.9) */
  }
  
  .card-overlay-bottom {
    top: auto
  }
  
  .card-overlay-fade {
    opacity: .7
  }
  
  .card-overlay-hover:hover .card-overlay-fade {
    opacity: .9
  }
  
  [class*='card-overlay-slide'] {
    opacity: 0
  }
  
  .card-overlay-title h3 {
    margin-top: 0;
    margin-bottom: 24px
  }
  
  @media only screen and (min-width :320px) {
    .card-overlay.card {
      height: 163px
    }
  
    .card-overlay-title h3 {
      font-size: 13px
    }
  

  
    .card-overlay-slide-bottom {
      -webkit-transform: translateY(72%);
      transform: translateY(72%)
    }
  
    .card-overlay-fade,
    [class*='card-overlay-slide'] {
      transition-property: none
    }
  
    .card-overlay-hover:hover [class*='card-overlay-slide'] {
      opacity: .9;
      -webkit-transform: translateX(0) translateY(72%);
      transform: translateX(0) translateY(72%)
    }
  }
  
  @media only screen and (min-width :768px) {
  
    .card-overlay.card,
    .card-overlay {
      height: 100%
    }
  
    .col-sm-4 .card-overlay-slide-bottom {
      -webkit-transform: translateY(72%);
      transform: translateY(72%)
    }
  
    .col-sm-4 .card-overlay-title h3 {
      font-size: 16px
    }
  
    .col-sm-6 .card-overlay-slide-bottom {
      -webkit-transform: translateY(81%);
      transform: translateY(81%)
    }
  
  }
  
  @media only screen and (min-width :992px) {
    .col-sm-4 .card-overlay-slide-bottom {
      -webkit-transform: translateY(75%);
      transform: translateY(75%)
    }
  
    .col-sm-4 .card-overlay-title h3 {
      font-size: 21px
    }
  
    .col-sm-6 .card-overlay-slide-bottom,
    .col-md-6 .card-overlay-slide-bottom {
      -webkit-transform: translateY(85.5%);
      transform: translateY(85.5%)
    }
  
  
    .card-overlay-fade,
    [class*='card-overlay-slide'] {
      transition-duration: .3s;
      transition-timing-function: ease-out;
      transition-property: opacity, transform, filter
    }
  
    .card-overlay-active .card-overlay-fade,
    .card-overlay-active .card-overlay-scale,
    .card-overlay-active .card-overlay-spin,
    .card-overlay-active [class*='card-overlay-slide'] {
      transition-duration: .8s
    }
  
    .card-overlay-hover:hover [class*='card-overlay-slide'] {
      opacity: 1;
      -webkit-transform: translateX(0) translateY(0);
      transform: translateX(0) translateY(0)
    }
  }
  
  @media only screen and (min-width :1200px) {
    .col-sm-4 .card-overlay-slide-bottom {
      -webkit-transform: translateY(80%);
      transform: translateY(80%)
    }
  
  
    .col-sm-6 .card-overlay-slide-bottom,
    .col-md-6 .card-overlay-slide-bottom {
      -webkit-transform: translateY(88%);
      transform: translateY(88%)
    }
  
  }
  
  .card-overlay-hover:hover [class*="card-overlay-slide"] {
    opacity: 1;
    -webkit-transform: translateX(0) translateY(0);
    transform: translateX(0) translateY(0)
  }


